.dark-grey {
  background-color: $black;
  color: $ghost
}

.dark-grey pre {
  background-color: $black;
  padding: 0;
  border: none
}

.dark-grey pre code {
  color: #00bcd4
}

.dark-grey h1 a, .dark-grey h2 a, .dark-grey h3 a, .dark-grey h4 a, .dark-grey h5 a {
  color: $ghost
}

.dark-grey code, .dark-grey strong {
  color: $white
}

.dark-grey code {
  font-weight: 100
}

.dark-grey table {
  color: $ghost
}

.dark-grey table td, .dark-grey table th {
  border-color: #444
}

.dark-grey table tbody td:first-child {
  color: $white
}

.dark-grey .form-group label {
  color: $ghost;
  border-color: rgba(95, 95, 95, .78)
}

.dark-grey .form-group.form-textarea label:after {
  background-color: $black
}

.dark-grey .form-control {
  color: $ghost;
  border-color: rgba(95, 95, 95, .78)
}

.dark-grey .form-control:focus {
  border-color: $ghost;
  color: $ghost
}

.dark-grey textarea.form-control {
  color: $ghost
}

.dark-grey .card {
  border-color: rgba(95, 95, 95, .78)
}

.dark-grey .card .card-header {
  background-color: transparent;
  color: $ghost;
  border-bottom: 1px solid rgba(95, 95, 95, .78)
}

.dark-grey .btn.btn-ghost.btn-default {
  border-color: #ababab;
  color: #ababab
}

.dark-grey .btn.btn-ghost.btn-default:focus, .dark-grey .btn.btn-ghost.btn-default:hover {
  border-color: #9c9c9c;
  color: #9c9c9c;
  z-index: 1
}

.dark-grey .btn.btn-ghost.btn-default:focus, .dark-grey .btn.btn-ghost.btn-default:hover {
  border-color: #e0e0e0;
  color: #e0e0e0
}

.dark-grey .btn.btn-ghost.btn-primary:focus, .dark-grey .btn.btn-ghost.btn-primary:hover {
  border-color: $primary;
  color: $primary
}

.dark-grey .btn.btn-ghost.btn-success:focus, .dark-grey .btn.btn-ghost.btn-success:hover {
  border-color: $success;
  color: $success
}

.dark-grey .btn.btn-ghost.btn-info:focus, .dark-grey .btn.btn-ghost.btn-info:hover {
  border-color: $info;
  color: $info
}

.dark-grey .btn.btn-ghost.btn-error:focus, .dark-grey .btn.btn-ghost.btn-error:hover {
  border-color: $error;
  color: $error
}

.dark-grey .btn.btn-ghost.btn-warning:focus, .dark-grey .btn.btn-ghost.btn-warning:hover {
  border-color: $warning;
  color: $warning
}

.dark-grey .avatarholder, .dark-grey .placeholder {
  background-color: transparent;
  border-color: #333
}

.dark-grey .menu .menu-item {
  color: $ghost;
  border-color: rgba(95, 95, 95, .78)
}

.dark-grey .menu .menu-item.active, .dark-grey .menu .menu-item:hover {
  color: $white;
  border-color: $ghost
}